import { EDIT_RULE, ADD_RULE, DELETE_RULE, RESET_RULES } from 'actions';
const initialState = [
    'Each Pokmon that faints is considered dead and must be released or permaboxed',
    'You can only catch the first Pokemon you encounter in an area',
    'All Pokmon must be nicknamed',
];
export function rules(state = initialState, action) {
    switch (action.type) {
        case ADD_RULE:
            return [...state, ''];
        case EDIT_RULE:
            const newState = state;
            newState[action.target] = action.rule;
            return newState;
        case DELETE_RULE:
            return state.filter((_, i) => i !== action.target);
        case RESET_RULES:
            let newState2 = state;
            newState2[2] = '';
            newState2[1] = '';
            newState2[0] = initialState[0];
            newState2 = initialState;
            return newState2;
        default:
            return state;
    }
}
