export const NEW_NUZLOCKE = 'NEW_NUZLOCKE';
export function newNuzlocke(initialData) {
    return {
        type: NEW_NUZLOCKE,
        initialData,
    };
}
export const DELETE_NUZLOCKE = 'DELETE_NUZLOCKE';
export function deleteNuzlocke(tid) {
    return {
        type: DELETE_NUZLOCKE,
        tid,
    };
}
export const SWITCH_NUZLOCKE = 'SWITCH_NUZLOCKE';
export function switchNuzlocke(currentNuzlockeId, newNuzlockeId) {
    return {
        type: SWITCH_NUZLOCKE,
        currentNuzlockeId,
        newNuzlockeId,
    };
}
